<template>
    <div>
        <!-- 头部 -->
        <van-nav-bar fixed title="地址管理" left-arrow @click-left="$router.back()"
            style="border-bottom: 0.02667rem solid #dcdcdc;margin-bottom: 10px;">
            <template #right>
                <van-icon name="ellipsis" size="18"  />
            </template>
        </van-nav-bar>


        <!--  -->
        <van-address-list v-model="chosenAddressId" :list="list" default-tag-text="默认" @add="onAdd" @edit="onEdit" />
    </div>
</template>

<script>
import { getAddressList } from "@/api/index"
export default {
    name: "AddressList",
    props: [],
    created() {
        // console.log(this.$route.query.cartItemId);
        this.cartId = this.$route.query.cartItemId
        this.getData();
    },
    data() {
        return {
            chosenAddressId: '', //当前选中地址的id
            list: [],//地址列表数据
            cartId: [],
        }
    },
    // 侦听器
    watch: {
        chosenAddressId(val) {
            // console.log(val); //侦听选择地址的变化
            // this.$router.push("/create-order?addressId=" + val)
            this.$router.push({
                path: "/create-order",
                query:
                {
                    addressId: val,
                    cartItemId: this.cartId
                }

            })
        }

    },
    methods: {
        // 新增地址
        onAdd() {
            this.$router.push('/addressedit')
        },
        // 编辑地址
        onEdit(item) {
            console.log(item);
            this.$router.push('/addressedit?addressId=' + item.id)
        },
        getData() {
            getAddressList().then(data => {
                if(data.resultCode===200){
                    this.list = data.data.map(item => {
                        return {
                            id: item.addressId,
                            name: item.userName,
                            tel: item.userPhone,
                            address: item.provinceName + item.cityName + item.regionName + item.detailAddress,
                            isDefault: item.defaultFlag

                        }
                    })
                }
                
            })
        }
    },

}
</script>

<style lang="less" scoped>

/deep/.van-nav-bar .van-icon {
    color: #252525;
}
/deep/.van-radio__icon {
    display: none;
}
.van-address-list{
   padding-top: 55px;
}
</style>